<template>
    <div>
        <loading :active.sync="isLoading" :is-full-page="true"></loading>

        <b-row>
            <b-col>
                <h2>
                    <v-icon style="width: 32px;" name="settings"></v-icon>
                       List Multimedia service chains
                </h2>
            </b-col>

            <b-col class="text-right">
            <b-button to="/chains/new" variant="success" class="mr-2">
                <v-icon name="plus"></v-icon>
                Create New Chain
            </b-button>    
        </b-col>
        </b-row>
        
        <b-table
            small
            :busy="isBusy"
            :items="items" 
            :fields="fields" 
            striped 
            responsive="sm">

            <template v-slot:="row" align-v="center">
                SDAS
            </template>

            <template v-slot:="row">
                 SDAS        
            </template>

            <template v-slot:="row">
                SDASDAS
            </template>

            <template v-slot:="row">
                
            </template>

            <template v-slot:="row">
            
                <b-button variant="success" size="sm" @click="bindSrc(row.item)" class="mr-2">
                    <v-icon name="minimize-2"></v-icon>
                    Bind / Unbind
                </b-button>
            </template>

            <div slot="table-busy" class="text-center text-danger my-2">
                <b-spinner class="align-middle"></b-spinner>
                <strong>Loading...</strong>
            </div> 

        </b-table>

        <b-alert
            variant="secondary"
            class="text-center"
            :show=!items.length>
            There are no Service Chain yet!            
        </b-alert>

        <b-row>
            <b-col class="text-right">
                <strong>Total: {{items.length }}</strong>
            </b-col>
        </b-row>

    </div>
</template>

<script>

import Loading from 'vue-loading-overlay';
import { apiChain } from './api'

export default {    
    name: 'index',
    components: {Loading},
    data() {
        return {
            isBusy: true,
            isLoading: false,
            sdp: '',
            fields: [{
                key: 'name',
                label: 'Name'
            }, {
                key: 'description',
                label: 'Description'
            },{
                key: 'subnet',
                lebel: 'Sub Network'
            },{
                key: 'actions',
                class: 'actions'
            }],
            items:[],
            msg: {
                text: false,
                type: '',
                show: false
            }
        }
    },

    methods: {

        monitor(){

        },
        refresh() {
            this.isLoading = false;
            apiChain.getAllChain()
                .then((resp) => {
                    this.items = resp
                    this.isBusy = false
                    this.isLoading = false
                })
                .catch(e => {
                    console.log(e);
                    this.isBusy = false
                    this.isLoading = false
                })
        }
    },
    created() {
        this.refresh()
    }
}


</script>
